import { Component, ViewChild, ElementRef } from '@angular/core';
import { TodoDataService } from './todo-data.service';
import { Todo } from './todo';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'TodoApp';
  newTodo: Todo = new Todo();
  todos: Todo[];
  
  // updateTitle;
  conVar;

  @ViewChild('title') nameField: ElementRef;
  updateId: any;
    focusInput(): void {
    this.nameField.nativeElement.focus();
    }


  constructor(private todoDataService: TodoDataService) {
    this.getAllInit();
      // this.todos = this.todoDataService.getAllTodos();
  }

  updateTitle(tododo) {
    this.newTodo.title = tododo.title;
    this.focusInput();
  }

  getAllInit() {
    this.todoDataService.getConfig().subscribe(result => {
      this.todos = result;
      console.log(this.todos);

    });
  }

  addTodo() {
    this.todoDataService.addTodo(this.newTodo).subscribe(res => {
      this.getAllInit();
      console.log(res);
    });
    this.newTodo = new Todo();
  }

  removeTodo(id: number) {
    this.todoDataService.deleteTodoById(id).subscribe(() => {
      this.getAllInit();
    });
    console.log('Pushed!!!!!');
  }

  updateTodo(id: number) {
    this.todoDataService.updateTodoById(this.newTodo, id).subscribe(() => {
        this.getAllInit();
        this.updateId = undefined;
        this.newTodo = {} as Todo;
       });
       console.log('Pushed update$$$');
   }

  toggleTodo(todo: Todo) {
    this.todoDataService.toggleTodo(todo);
  }
}
