import { Injectable } from '@angular/core';
import { Todo } from './todo';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';


const httpOptions = {
  headers:
    new HttpHeaders({
      'Content-Type': 'application/json'
    }),
    withCredentials: true

};





@Injectable({
  providedIn: 'root'
})
export class TodoDataService {

  todos: Todo[];
  lastId: 0;

  constructor(private http: HttpClient) {
    this.todos = [];
  }

  getUrl = '/api/get_todos';
  postUrl = '/api/create_todo';

getConfig() {
  return this.http.get<Todo[]>(this.getUrl, {withCredentials: true });


}

  addTodo(todo): Observable<Todo> {
    this.todos = [];
    todo.id = ++this.lastId;
    this.todos.push(todo);
    return this.http.post<any>(this.postUrl, this.todos, httpOptions);
  }

  deleteTodoById(id): Observable<void> {
    const deleteUrl = `/api/delete_todo/${id}`;
    console.log(deleteUrl);
    return this.http.delete<void>(deleteUrl);
  }

  updateTodoById(todo: Todo, id: number): Observable <any> {
    const updateUrl = '/api/update_todo/' + id;
    return this.http.put<any>(updateUrl, todo);
  } 

  getTodoById(id: number): Todo {
    return this.todos.filter(todo => todo.id === id).pop();
  }

  getAllTodos(): Todo[] {
    return this.todos;
  }

  toggleTodo(todo: Todo) {
    todo.complete = !todo.complete;
    return this.http.put<any>(this.postUrl, this.todos, httpOptions);
  }

  // updateTodoById(id: number) {

  // }
}
